.tab {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: white;
  border-top: 1px solid #eee;
}
.tab .item {
  display: inline-block;
  width: 25%;
  padding: 5px 0;
  text-align: center;
  line-height: 1;
}
.tab .item .text {
  font-size: 16px;
  margin-top: 3px;
}
.tab .item .icon {
  font-size: 20px;
}
.tab .active {
  color: blue;
}

.sub {
  text-align: center;
  letter-spacing: 1px;
}
.sub .title {
  font-size: 16px;
}
.sub .box {
  position: relative;
  padding-bottom: 5px;
}
.sub .box .Subheading {
  padding: 5px 25px;
  border-bottom: 1px solid #eee;
}
.sub .box .more {
  position: absolute;
  right: 5px;
  top: 0px;
  font-size: 12px;
  padding: 2px 5px;
  display: inline-block;
}

.goodlist {
  background: #f5f6f7;
  letter-spacing: 1px;
}
.goodlist .item {
  background: white;
  display: flex;
  padding: 8px 0px;
  margin-bottom: 3px;
}
.goodlist .item .img {
  flex: 0 0 120px;
  width: 120px;
  height: 120px;
}
.goodlist .item .des {
  flex-grow: 1;
  padding-left: 5px;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
}
.goodlist .item .des .name {
  width: 100%;
  padding-top: 5px;
}
.goodlist .item .des .box {
  padding-bottom: 5px;
}

.articlelist {
  background: #f5f6f7;
  letter-spacing: 1px;
}
.articlelist .test {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
}
.articlelist .item {
  background: white;
  padding: 8px 0px;
  margin-bottom: 3px;
  display: flex;
}
.articlelist .item .img {
  flex: 0 0 120px;
  width: 120px;
  height: 120px;
}
.articlelist .item .des {
  flex-grow: 1;
  padding-left: 5px;
}
.articlelist .item .des .name {
  text-align: center;
  width: 100%;
}
.articlelist .item .des .content {
  text-indent: 2em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 4;
  text-align: justify;
}

.herdtitle {
  padding: 0px 10px;
  background: blue;
  color: white;
  line-height: 45px;
}
.herdtitle .icon-fanhui {
  font-size: 20px;
  line-height: 45px;
  vertical-align: middle;
}
.herdtitle .left {
  line-height: 1;
  position: absolute;
  width: 40%;
  height: 45px;
  left: 10px;
  top: 0;
}
.herdtitle .left .titleleft {
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  margin-left: 20px;
}
.herdtitle .left .titleleft .icon {
  font-size: 20px;
  line-height: 20px;
}
.herdtitle .left .titleleft p {
  font-size: 12px;
  line-height: 13px;
}
.herdtitle .tit {
  width: 100%;
  text-align: center;
}
.herdtitle .right {
  width: 40%;
  position: absolute;
  right: 10px;
  top: 0;
  line-height: 1;
}
.herdtitle .right .box {
  height: 45px;
}
.herdtitle .right .item {
  float: right;
  text-align: center;
  margin-top: 5px;
  margin-left: 10px;
}
.herdtitle .right .item .icon {
  font-size: 20px;
  line-height: 20px;
}
.herdtitle .right .item p {
  font-size: 12px;
  line-height: 13px;
}

.banner {
  height: 200px;
}
.contranal {
  margin-top: 20px;
  padding: 0 10px;
}

.crumbs {
  overflow: hidden;
  padding: 10px 10px;
  background: #f5f5f5;
}
.crumbs .item {
  float: left;
}
.crumbs .item a {
  margin-left: 5px;
}

.classification {
  width: 100%;
  border: 1px solid #e8dbdb;
  border-collapse: collapse;
  line-height: 35px;
}
.classification tr td {
  border: 1px solid #e8dbdb;
}
.classification tr .items {
  overflow: hidden;
}
.classification tr .items .item {
  cursor: pointer;
  float: left;
  padding: 0 10px;
}
.classification tr .items .active {
  background: blue;
  color: white;
}

.sort {
  overflow: hidden;
  border: 1px solid #e8dbdb;
}
.sort .item {
  cursor: pointer;
  float: left;
  padding: 5px 15px;
}
.sort .item .icon {
  font-size: 14px;
  vertical-align: middle;
  margin-left: 1px;
}


.container .row {
  padding: 0 10px;
}
.container .Crumbsbox {
  margin: 20px 0;
}
.container .Classificationbox {
  margin: 20px 0;
}


.personal_title {
  padding: 0px 10px;
  overflow: hidden;
}
.personal_title .left {
  float: left;
}
.personal_title .right {
  float: right;
}

.personal .herder {
  background: blue;
  padding: 15px 0;
  color: white;
}
.personal .herder .top {
  padding-left: 30px;
}
.personal .herder .top img {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  display: inline-block;
}
.personal .herder .top .per {
  display: inline-block;
  vertical-align: middle;
  margin-left: 5px;
}
.personal .herder .bottom {
  margin-top: 10px;
}
.personal .herder .bottom .item {
  display: inline-block;
  width: 33.333%;
  text-align: center;
}
.personal .row {
  padding: 10px 0px;
  border-bottom: 1px solid #e8dbdb;
}
.personal .row .items {
  overflow: hidden;
  margin-top: 5px;
}
.personal .row .items .item {
  float: left;
  width: 20%;
  text-align: center;
  color: black;
}
.personal .row .items .item .icon {
  font-size: 20px;
  line-height: 1;
}
.personal .row .items .item p {
  font-size: 14px;
  line-height: 1;
}
.personal .countbox {
  text-align: center;
  margin-top: 20px;
}
.personal .countbox .count {
  border: 1px solid #e8dbdb;
  display: inline-block;
  font-size: 0;
}
.personal .countbox .count span {
  display: inline-block;
  line-height: 20px;
  font-size: 14px;
}
.personal .countbox .count .Calculation {
  width: 25px;
  cursor: pointer;
}
.personal .countbox .count .num {
  width: 40px;
  border-left: 1px solid #e8dbdb;
  border-right: 1px solid #e8dbdb;
}
.personal .Route {
  margin-top: 20px;
}
.personal .Route .item {
  width: 33.33%;
  display: inline-block;
  line-height: 20px;
  text-align: center;
}

.order .orderitem {
  border-bottom: 1px solid #e8dbdb;
  background: white;
  padding: 10px 0;
}
.order .orderitem .titlebox {
  overflow: hidden;
  padding: 0 10px;
}
.order .orderitem .titlebox .left {
  float: left;
}
.order .orderitem .titlebox .right {
  float: right;
}
.order .orderitem .content {
  overflow: hidden;
  padding: 5px 10px;
}
.order .orderitem .content .left {
  float: left;
}
.order .orderitem .content .left img {
  width: 80px;
  height: 80px;
  display: inline-block;
}
.order .orderitem .content .left .des {
  display: inline-block;
  vertical-align: top;
  margin-left: 5px;
  width: 120px;
}
.order .orderitem .content .right {
  float: right;
  text-align: left;
}
.order .orderitem .all {
  text-align: right;
  margin-bottom: 5px;
  padding: 0 10px;
}
.order .orderitem .lists {
  display: flex;
  justify-content: space-around;
}
.order .orderitem .lists .list {
  padding: 0 15px;
  border: 1px solid #e8dbdb;
  line-height: 25px;
  border-radius: 30px;
}

*{
	margin: 0;
	padding: 0;
}
body{
	/*background: #f5f6f7;*/
    -webkit-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;	
}
p,ul{
	margin: 0;
}
ul{
	list-style: none;
}

